.title {
    text-align: center;
}

.orders-table {
    width: 90%;
    margin: 0 auto;
    border-collapse: collapse;
}

.orders-table th,
.orders-table td {
    border: 1px solid black;
    padding: 8px;
    text-align: left;
}

.orders-table th {
    background-color: #f2f2f2;
}

.NotificationHome {
    width: 100%;
    max-width: 800px;
    /* 最大幅を800pxに設定 */
    margin: 50px auto;
    padding: 30px;
    border: 1px solid #e0e0e0;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
    text-align: center;
    /* ボタンを中央揃え */
}

.NotificationHome h1 {
    font-size: 32px;
    /* タイトルのフォントサイズを大きく */
    margin-bottom: 25px;
    color: #333;
}

.NotificationHome button {
    width: 100%;
    padding: 14px;
    margin-bottom: 15px;
    background-color: #4fc3f7;
    border-radius: 2px;
    border-color: #4fc3f7;
    color: white;
    font-size: 20px;
    /* フォントサイズを大きく */
    cursor: pointer;
}

.NotificationHome button:hover {
    background: #ffffff;
    color: #4fc3f7;
}